.app__wrapper_info {
    padding-top: 2rem;
}

.app__chef-content {
    width: 100%;
    margin-top: 5rem;
}

.app__chef-content img {
    width: 47px;
    height: 40px;
    margin: 0 1rem 1rem 0;
    float: left;
}

.app__chef-sign {
    width: 100%;
    margin-top: 3rem;
}

.app__chef-sign p:first-child {
    font-family: var(--font-base);
    font-size: 32px;
    font-weight: 400;
    line-height: 41px;
    color: var(--color-golden);
    letter-spacing: 0.04em;
    text-transform: capitalize;
}

.app__chef-sign img {
    width: 250px;
    margin-top: 3rem;
}


@media screen and (max-width: 650px) {
    .app__chef-sign img {
        width: 80%;
    }

    .app__chef-content {
        margin-top: 2rem;
    }
    
    .headtext__cormorant {
        font-size: 38px;
    }
}