.app__aboutus {
    position: relative;
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 391px;
    height: 415px;
    z-index: 0;
}

.app__aboutus-content {
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: right;
}

.app__aboutus-content_knife {
    margin: 2rem 6rem;
}

.app__aboutus-content_knife img {
    height: 700px;
}

.app__aboutus-content_history {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;
}

.app__aboutus-content_history p,
.app__aboutus-content_about p {
    margin: 2rem 0px;
    color: var(--color-grey);
}


@media screen and (max-width: 991px) {
    .app__aboutus-content {
        flex-direction: column;
    }

    .app__aboutus-content_about {
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .app__aboutus-content_history{
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}
