.app__header {
    background-color: var(--color-black);
}

.app__header-h1 {
    font-family: var(--font-base);
    color: var(--color-golden);
    text-transform: capitalize;
    line-height: 117px;
    font-size: 90px;
}

.app__header-img img{
    width: 80%;
}


@media screen and (max-width: 991px) {
    .app__header-h1 {
        line-height: 5rem;
        font-size: 5rem;
    }
    .app__header p {
        font-size: 1rem;
    }
    
}