.app__specialMenu {
    flex-direction: column;
    background-color: var(--color-black);
}

.app_specialMenu-title {
    text-align: center;
    margin-bottom: 2rem;
}

.app__specialMenu-menu {
    width: 100%;
    margin: 2rem 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;

}

.app__specialMenu-menu_wine,
.app__specialMenu-menu_cocktails {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.app__specialMenu-menu_heading {
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 45px;
    line-height: 58.5px;
    letter-spacing: 0.04rem;
    color: var(--color-white);
}

.app__specialMenu-menu_img {
    width: 410px;
    margin: 0 2rem;
}

.app__specialMenu-menu_img img {
    width: 100%;
}

.app__specialMenu_menu_items {
    display: flex;
    flex-direction: column;
    width: 100%;
}


@media screen and (max-width: 991px) {
    .app__specialMenu-menu {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .app__specialMenu-menu_wine, .app__specialMenu-menu_cocktails {
        width: 100%;
    }

    .app__specialMenu-menu_img {
        margin-bottom: 3rem;
    }
}

@media screen and (max-width: 650px) {
    
    .app__specialMenu-menu_img {
        width: 100%;
    }

    .app__specialMenu-menu_heading {
        font-size: 35px;
        line-height: 48.5px;
    }

}