.app__menuitem {
    width: 100%;
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
}

.app__menuitem-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}


.app__menuitem-name {
    text-wrap: nowrap;
}

.app__menuitem-dash {
    width: 100%;
    height: 1px;
    background-color: var(--color-white);
    margin: 0 1rem;
}

.app__menuitem-price {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    font-size: 1.3em;
}

.app__menuitem-sub {
    margin-top: .2rem;
}